#protected {
    display: flex;
    background-color: rgb(255, 255, 255);
    width: 100vw;
    align-items: center;
    column-gap: 10px;
    justify-content: center;
    margin-top: 150px;

}

#inner {
    display: flex;
    
}

#clouds img {
    
    width: 100%;
    height: auto;
}
@media screen and (max-width: 850px) {
    #inner {
        flex-direction: column;
    }
    #protected {
        margin-top: 0px;
    }

}