#home {
    display: flex;
    font-family: 'Roboto Condensed';
    font-size: 1.1rem;
    background-color: rgb(87, 56, 87);
    color:#fff;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}
#chatbox {
    width: 100%;
    display: flex;
    flex-direction: column;
}
#online-users {
    display: flex;
    padding-inline:15 5px 15px 5px;
    margin-bottom: 7px;
    justify-content: center;
}
#messages {
    display: flex;
    flex-direction: column;
    row-gap: 5px;
    overflow-y: auto;
    max-height:500vh;
}
#send {
    display: flex;
}
#dialog {
    font-family: 'Roboto Condensed';
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding:15px;
    background-color: rgb(32,32,32);
    color:#fff;
}
#dialog div {
    display: flex;
    align-items: center;
}
#close-dialog {
    cursor: pointer;
}